.box-search {
  
  position: relative;
  
  display: flex;
  flex-direction: column;
  gap: 30px;
  transition: all 0.5s;

}

.input-div {
  position: relative;
}

.form-input {
  width: 224px;
  font-size: 14px;
  outline: none;

  padding: 10px;
  padding-left: 20px;
  transition: 0.5s;
  background-color: transparent;
  padding: 10px;
  border: 1px solid $light-grey-color;
  border-radius: 8px;
  color: $white-color;
  color: $light-grey-color;
}

.form-input ~ span {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;

  align-items: center;
  text-align: left;
  color: $light-grey-color;

  position: absolute;
  left: 10px;
  top: 7px;
  padding: 5px;
  transition: 0.3s;

  background-color: $black-color;

  letter-spacing: 1px;

  z-index: -1;
}

.form-input:valid ~ span,
.form-input:focus ~ span {
  transform: translateY(-7px);
  top: 0;
  padding: 0 10px;
  border-left: 2px solid white;
  border-right: 2px solid white;
  color: white;
  font-size: 0.8rem;
  z-index: 1;
}

.form-input:focus,
.form-input:valid {
  border: 2px solid white;
  color: white;
}

//

.search-form button[type='submit'],
.load-more {
  width: 40px;
  height: 40px;
  padding: 11px;

  background: linear-gradient(
      141.22deg,
      $gradient-orange-color 9.4%,
      $gradient-red-color 91.91%
    )
    border-box;

  border-radius: 50%;
  transition: color $transition $transition-cubic-bezier;
  border: 1px solid transparent;
}

.search-svg-btn {
    stroke: $white-color;
    fill: transparent;
}

.search-form button[type='submit']:focus-visible,
.search-form button[type='submit']:hover,
.search-svg-btn:focus-visible,
.search-svg-btn:hover {
  text-decoration: none;
  color: $orange-color;
  // TODO: Заменить $black-color на CSS переменную темы
  background: linear-gradient($white-color, $white-color) padding-box,
    linear-gradient(
        141.22deg,
        $gradient-orange-color 9.4%,
        $gradient-red-color 91.91%
      )
      border-box;
      
}

.search-form button[type='submit']:focus-visible .search-svg-btn,
.search-form button[type='submit']:hover .search-svg-btn {
    stroke: $orange-color;
    fill: transparent;
}

