.form-input,
.clear-btn,
.search-wrapper {
    position: relative;
    
}

.search-wrapper {
    width: 500px;
    margin: auto;
    margin-top: 50px;
}





.clear-btn {
    border: 1px solid transparent;
    background-color: transparent;
    display: inline-block;

    display: none;
    vertical-align: middle;
    outline: 0;
    cursor: pointer;
    
}

.clear-btn:after {
    content: "X";
    display: block;
    width: 26px;
    height: 26px;
    position: absolute;
    background-color: $darker-grey-color;
    z-index: 1;
    right: 14px;
    top: 0;
    bottom: 0;
    margin: auto;
    padding: 6px;
    border-radius: 8px;
    text-align: center;
    color: white;
    font-weight: normal;
    font-size: 14px;
    box-shadow: 0 0 2px $grey-color;
    cursor: pointer;
    
}

.clear-btn.is-active {
    display: inline-block;
    
}

