.modal-backdrop {
    position: fixed;
    z-index: 20;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 1;
    background-color: $black-color;
    &.is-hidden {
            opacity: 0;
            pointer-events: none;
            visibility: hidden;
        }
    }
.modal__wrap {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 280px;
    background-color: $black-color; //!
    padding: 52px 16px;
    transition: transform 0.25s cubic-bezier(0.4, 0, 0.2, 1),
        opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
    z-index: 90;
    overflow-x: auto;
    box-shadow: 1px 1px 14px 4px rgba(255, 107, 8, 0.42);
    border-radius: 20px;


    @include media-query(tablet) {
        min-height: 488px;
        width: 704px;
        padding: 44px;
    }

    @include media-query(desktop) {
        min-height: 598px;
        width: 846px;
        padding: 60px;
    }
}
.modal__close-btn {
    position: absolute;
    top: 20px;
    right: 16px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    color: $white-color;
    border: none;
    border-radius: 50%;
    background-color: $black-color;
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    cursor: pointer;

    @include media-query(tablet) {
        top: 24px;
        right: 24px;
    }

    @include media-query(desktop) {
        top: 40px;
        right: 40px;
        width: 30px;
        height: 30px;
    }

    &:hover {
        color: $orange-color;
    }
}

.modal__close-icon {
    fill: currentColor;
}

